import React, { FC } from "react";
import { TitleDecorator } from "pages/Home/components/TitleDecorator";
import css from "./style.module.scss";
import { Arrows } from "./Arrows";
import { useHistory } from "react-router-dom";

interface Props {
  title: string;
  allLink?: string | null;
  increment: () => void;
  decrement: () => void;
}

export const Header: FC<Props> = ({ title, allLink, increment, decrement }) => {
  const history = useHistory();

  return (
    <div className={css.slots_header}>
      <div className={css.decor_with_buttons}>
        <TitleDecorator position="right">{title}</TitleDecorator>

        <Arrows
          onClicks={{
            leftArrow: decrement,
            rightArrow: increment,
          }}
        />
      </div>

      {allLink === null ? null : (
        <TitleDecorator
          onClick={() => {
            allLink && history.push(allLink);
          }}
          position="left">
          View All
        </TitleDecorator>
      )}
    </div>
  );
};
