import React, { useEffect, useMemo, useRef, useState } from "react";
import clsx from "clsx";

import { useChat, useWindowSize } from "providers";
import { repeat } from "utils";

import { EmptyGameLabel, GameBanner } from "./components";
import css from "./style.module.scss";
import { IBannerProps } from "../../MenuListSection";

let desktopSize = parseInt(css.desktopCard);
const padding = 5;

const mobileSize = 400;

export const BannersList = ({
  contentData,
  withoutBigBanners = false,
}: {
  contentData: IBannerProps[];
  withoutBigBanners?: boolean;
}) => {
  const rootRef = useRef<HTMLDivElement | null>(null);
  const { width } = useWindowSize();
  const { isHide } = useChat();
  const [emptyAmount, setEmptyAmount] = useState(0);
  const isMobile = width < mobileSize;
  let bannersLength = contentData.length;
  if (!withoutBigBanners) {
    bannersLength += 1;
  }
  const calculateEmptyAmount = (node: HTMLDivElement | null) => {
    if (!node || isMobile) {
      node && node.style.setProperty("--columnCount", `2`);
      setEmptyAmount(0);
      return;
    }
    const { offsetWidth } = node;
    if (isMobile) {
      desktopSize = parseInt(css.mobileCard);
    }

    const cellsOnRowAmount = Math.floor(offsetWidth / (desktopSize + 15));

    node.style.setProperty("--columnCount", `${cellsOnRowAmount}`);

    let emptyAmount = cellsOnRowAmount - (bannersLength % cellsOnRowAmount);

    if (emptyAmount === cellsOnRowAmount) {
      emptyAmount = 0;
    }
    setEmptyAmount(emptyAmount);
  };

  useEffect(() => {
    calculateEmptyAmount(rootRef.current);
  }, [width, isHide]);

  return (
    <div
      ref={(node) => {
        rootRef.current = node;
        calculateEmptyAmount(node);
      }}
      className={css.listRoot}>
      {contentData.map(({ url, to, label, info }, i) => {
        const isBig = !withoutBigBanners && i === 0;
        return (
          <GameBanner
            key={url + label}
            to={to}
            label={label}
            info={info}
            bigBanner={isBig}>
            <img
              src={url}
              className={clsx(css.bannerImg, isBig && css.bigImg)}
            />
          </GameBanner>
        );
      })}

      {!isMobile ? (
        repeat(emptyAmount, () => (
          <EmptyGameLabel className={css.emptyBannerImg} />
        ))
      ) : bannersLength % 2 ? (
        <EmptyGameLabel className={css.emptyBannerImg} />
      ) : null}
    </div>
  );
};
