import React, { FC, Suspense } from "react";

import { LazyHider } from "components";
import { useLocalStorage } from "hooks";

import LazyComponentLoader from "config/LazyComponentLoader";
import JoinNowBlock from "./JoinNowBlock";
import css from "./style.module.scss";

const User = React.lazy(() => LazyComponentLoader(import("./User")));

export interface LeaderboardUser {
  net_profit_today: string;
  user_name: string;
  profile_picture_url: string;
}

interface LeaderBoardProps {
  data?: LeaderboardUser[];
}

const elementSize = {
  height: 210,
  width: 240,
};

const LeaderBoardRow = ({ data }: LeaderBoardProps) => {
  const [token] = useLocalStorage("token");

  return (
    <section className={css.winnersBlock}>
      {data?.map(
        ({ user_name, net_profit_today, profile_picture_url }, index) => (
          <Suspense fallback={<div style={elementSize} />}>
            <LazyHider sizes={elementSize}>
              <User
                {...{
                  user_name,
                  net_profit_today,
                  profile_picture_url,
                  index,
                }}
              />
            </LazyHider>
          </Suspense>
        ),
      )}
      {!token && <JoinNowBlock />}
    </section>
  );
};

export default React.memo(LeaderBoardRow);
