import React, { useEffect } from "react";
import { useHistory } from "react-router-dom";

import { Modal, Transition } from "components";
import { useFetch } from "hooks";
import { ModalsProps } from "config/routes";

import Loading from "../Loading";
import SuccessMessage from "../SuccessMessage";
import ErrorMessage from "../ErrorMessage";
import css from "./style.module.scss";

interface FetchData {
  success: boolean;
  message: string;
}

interface Props extends ModalsProps {
  fetchUrl: string;
}

const ConfirmEmail = ({ isOpen, code, fetchUrl }: Props) => {
  const history = useHistory();

  const [fetch, { loading, data, error }] = useFetch<FetchData>(fetchUrl);

  useEffect(() => {
    if (!isOpen || !code) {
      return;
    }

    fetch({ variables: { code } });
  }, [code]);

  const handleClose = () => {
    history.push({ pathname: "/" });
  };

  const renderView = () => {
    if ((isOpen && ((data && !data.success) || !code)) || error) {
      return <ErrorMessage message={error || data?.message} />;
    }

    return <SuccessMessage message={data?.message} />;
  };

  return (
    <Modal open={isOpen} onClose={handleClose}>
      <div className={css.innerWrapper}>
        <Transition
          className={css.loadingWrapper}
          animation="fade"
          in={loading}>
          <Loading />
        </Transition>
        <Transition animation="fade" in={!loading}>
          {renderView()}
        </Transition>
      </div>
    </Modal>
  );
};

export default ConfirmEmail;
