import React, { FC } from "react";

import { Typography } from "components";
import { ChallengeElementType } from "providers";

import { getFormattedDate } from "components/NotificationWrapper/components/ChallengesNotifications/Notification";

import gift from "assets/homepage/challenges/gift.svg";
import clock from "assets/homepage/challenges/clock.svg";
import css from "../../style.module.scss";

export const Card: FC<ChallengeElementType> = ({
  id,
  title,
  image_url,
  description,
  reward_coin,
  reward_amount,
  end_time,
  max_win_users_count,

  challenge_type,
  cur_win_users_count,
  game,
  min_bet,
  min_win_multiplier,
  total_profit_amount,
  wager_amount,
}) => {
  return (
    <div className={css.item}>
      <div className={css.dummy}></div>
      <div className={css.card_container}>
        <Typography variant="h5" className={css.card_title}>
          {title}
        </Typography>
        <div className={css.card_content}>
          <img src={image_url} alt="" className={css.card_image} />
          <div>
            <Typography variant="body2" className={css.card_description}>
              {description}
            </Typography>
            <div className={css.gift}>
              <img src={gift} alt="" className={css.icon} />
              <Typography variant="inherit" className={css.text_decoration}>
                {reward_amount}
                <img
                  src={`https://cdn.pasino.com/coins/${reward_coin.toLocaleLowerCase()}.png`}
                  className={css.coinImg}
                />
              </Typography>
            </div>
            <div className={css.gift}>
              <img src={clock} alt="" className={css.icon} />
              <Typography variant="inherit" className={css.end_time}>
                until {getFormattedDate(end_time)}
              </Typography>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
