import React, { Suspense } from "react";
import { useWindowSize } from "providers";
import { LazyHider } from "components";
import LazyComponentLoader from "config/LazyComponentLoader";

export * from "./types";

const BetsRow = React.lazy(() => LazyComponentLoader(import("./component")));

export const BetList = () => {
  const { width } = useWindowSize();

  const tableSize = {
    height: width > 600 ? 428 : 600,
  };
  return (
    <>
      <LazyHider sizes={tableSize} shouldHideIfOutOfFrame>
        <Suspense fallback={<div style={tableSize} />}>
          <BetsRow />
        </Suspense>
      </LazyHider>
    </>
  );
};
