import React, { DetailedHTMLProps, FC } from "react";

import { TitleDecorator } from "../TitleDecorator";

import css from "./style.module.scss";

interface IContentWrapper
  extends DetailedHTMLProps<React.HTMLAttributes<HTMLElement>, HTMLElement> {
  title?: string;
}

export const ContentWrapper: FC<IContentWrapper> = ({
  title,
  children,
  ...props
}) => {
  return (
    <>
      <TitleDecorator position="right">
        {title ? title.toLowerCase() : ""}
      </TitleDecorator>

      <section className={css.container} {...props}>
        {children}
      </section>
    </>
  );
};
