import React, { useMemo } from "react";
import { useGetLocale } from "providers";

import {
  Button,
  LinkWithSearch,
  LinkWithSearchProps,
  Typography,
} from "components";

import css from "./style.module.scss";

const GamesRow = () => {
  const intl = useGetLocale();

  const ForwardedLink = useMemo(
    () =>
      React.forwardRef<HTMLAnchorElement | null, LinkWithSearchProps>(
        ({ children, to, search, ...props }, ref) => (
          <LinkWithSearch to={to} search={search} {...props}>
            {children}
          </LinkWithSearch>
        ),
      ),
    [],
  );

  return (
    <section className={css.joinNowBlock}>
      <Typography className={css.joinNowText} variant="h3">
        {intl.formatMessage({ id: "homeScreen.join.title" })}
      </Typography>
      <Button
        className={css.joinNowButton}
        // @ts-ignore
        component={ForwardedLink}
        // @ts-ignore
        search={{ name: "page", value: "register" }}>
        {intl.formatMessage({ id: "homeScreen.join.button" })}
      </Button>
    </section>
  );
};

export default GamesRow;
