import React, { useMemo, useState } from "react";

import crash from "assets/homepage/all_games/crash.svg";
import hashdice from "assets/homepage/all_games/hash_dice.webp";
import plinko from "assets/homepage/all_games/plinko.webp";
import limbo from "assets/homepage/all_games/limbo.webp";
import videoPoker from "assets/homepage/all_games/video_poker.webp";
import diamonds from "assets/homepage/all_games/diamonds.webp";
import keno from "assets/homepage/all_games/keno.webp";
import roulette from "assets/homepage/all_games/roulette.webp";
import black_jack from "assets/homepage/all_games/black_jack.webp";
import mines from "assets/homepage/all_games/mines.webp";
import towers from "assets/homepage/all_games/towers.webp";
import fortune from "assets/homepage/all_games/fortune_quest.webp";

import {
  IFetchHomePageInfo,
  MainGameListType,
} from "pages/Home/types/homeInfo";
import { useGetLocale } from "providers";
import { ContentWrapper } from "../ContentWrapper";
import { HouseGames } from "../HouseGames";
import { MenuList } from "../MenuList";

import css from "../HouseGames/BannersList/style.module.scss";

export type ContentsType = "default" | MainGameListType;

interface IMenuListSection {
  homePageInfo: IFetchHomePageInfo | undefined;
}
export interface IBannerProps {
  url: string;
  localeName: string;
  to: string;
  label: string;
  info: string;
}
const houseGameBanners: IBannerProps[] = [
  {
    url: crash,
    localeName: "crash",
    to: "/crash",
    label: "crash",
    info: "",
  },
  {
    url: hashdice,
    localeName: "hashdice",
    to: "/hash-dice",
    label: "hash dice",
    info: "",
  },
  {
    url: plinko,
    localeName: "plinko",
    to: "/plinko",
    label: "plinko",
    info: "",
  },
  {
    url: limbo,
    localeName: "limbo",
    to: "/limbo",
    label: "limbo",
    info: "",
  },
  {
    url: videoPoker,
    localeName: "videoPoker",
    to: "/video-poker",
    label: "video poker",
    info: "",
  },
  {
    url: towers,
    localeName: "towers",
    to: "/towers",
    label: "towers",
    info: "",
  },
  {
    url: diamonds,
    localeName: "diamonds",
    to: "/diamonds",
    label: "diamonds",
    info: "",
  },
  {
    url: keno,
    localeName: "keno",
    to: "/keno",
    label: "keno",
    info: "",
  },
  {
    url: roulette,
    localeName: "roulette",
    to: "/roulette",
    label: "roulette",
    info: "",
  },
  {
    url: black_jack,
    localeName: "blackjack",
    to: "/blackjack",
    label: "blackjack",
    info: "",
  },
  {
    url: mines,
    localeName: "mines",
    to: "/mines",
    label: "mines",
    info: "",
  },

  {
    url: fortune,
    localeName: "fortune",
    to: "/fortune-quest",
    label: "fortune quest",
    info: "",
  },
];
const desktopSize = parseInt(css.desktopCard) * 2;
const preloadContainerStyle = { height: `${desktopSize}px` };

const MenuListSection = ({ homePageInfo }: IMenuListSection) => {
  const [contentName, setContentName] = useState<ContentsType>("default");
  const intl = useGetLocale();

  const { contentData, withoutBigBanners } = useMemo((): {
    contentData: IBannerProps[];
    withoutBigBanners: boolean;
  } => {
    let contentData: IBannerProps[] = [];
    let withoutBigBanners = false;
    if (contentName === "default" || !homePageInfo) {
      contentData = houseGameBanners.map((game) => {
        return {
          ...game,
        };
      });
    } else {
      const newContentData = homePageInfo.main_game_list[contentName].map(
        (game) => {
          const { id, image_url, name, description } = game;
          return {
            label: name,
            to: `slot/${id}`,
            url: image_url,
            info: description,
            localeName: "",
          };
        },
      );
      contentData = newContentData;
      withoutBigBanners = true;
    }
    return { contentData, withoutBigBanners };
  }, [contentName, homePageInfo]);

  if (!homePageInfo || !contentData)
    return <ContentWrapper style={preloadContainerStyle} />;
  return (
    <>
      <MenuList setContentName={setContentName} />
      <HouseGames
        title={intl.formatMessage({ id: `homeScreen.${contentName}` })}
        contentData={contentData}
        withoutBigBanners={withoutBigBanners}
      />
    </>
  );
};
export default MenuListSection;
