import React, { FC, useMemo } from "react";

import { useChat, useWindowSize } from "providers";

import { ContentsType } from "../MenuListSection";
import { menuArray } from "./utils/menuArray";
import MenuItem from "./MenuItem/index";

import css from "./style.module.scss";

interface IMenuListProps {
  setContentName: (name: ContentsType) => void;
}
const chatOpenStyle = {
  gridTemplateColumns: "repeat(2, 256px)",
  gridTemplateRows: "repeat(2, auto)",
};

export const MenuList: FC<IMenuListProps> = ({ setContentName }) => {
  const { isHide } = useChat();
  const { width } = useWindowSize();
  const innerStyle = useMemo(() => {
    let style = {};
    if (!isHide && width < 1400) {
      if (width < 600) {
        style = { ...chatOpenStyle, gridTemplateColumns: "repeat(2, 1fr)" };
      } else {
        style = { ...chatOpenStyle, columnGap: "12px" };
      }
    }
    return style;
  }, [isHide, width]);

  return (
    <>
      <div className={css.container}>
        <div className={css.inner} style={innerStyle}>
          {menuArray.map(({ id, description, imageUrl, contentName }) => (
            <MenuItem
              key={id}
              descr={description}
              img={imageUrl}
              onClickHandler={() => setContentName(contentName)}
            />
          ))}
        </div>
      </div>
    </>
  );
};
