import React, { forwardRef } from "react";
import { Link } from "react-router-dom";
import { Helmet } from "react-helmet";

import { Logo, Button, Typography } from "components";

import css from "./style.module.scss";

const errorTitle = "404: The page you are looking for isnt here";
const errorDescription =
  "You either tried some shady route or you came here by mistake. Whichever it is, try using the navigation.";

const ForwardedLink = forwardRef<HTMLAnchorElement | null>((props) => (
  <Link {...props} to={"/"} />
));

const NotFound = () => {
  return (
    <>
      <Helmet>
        <title>Pasino | 404: Page Not Found</title>
        <meta name="title" content={errorTitle} />
        <meta name="description" content={errorDescription} />
      </Helmet>
      <div className={css.wrapper}>
        <div className={css.innerWrapper}>
          <Typography variant="h1" className={css.title}>
            {errorTitle}
          </Typography>
          <Typography variant="h6" className={css.description}>
            {errorDescription}
          </Typography>
          <div className={css.logoWrapper}>
            <Logo
              className={css.logo}
              logoVariant={"notFoundPasino"}
              to={{ pathname: "/" }}
            />
          </div>
          <div className={css.buttonWrapper}>
            <Button
              className={css.button}
              // @ts-ignore
              component={ForwardedLink}>
              BACK TO HOME
            </Button>
          </div>
        </div>
      </div>
    </>
  );
};

export default NotFound;
