import React from "react";
import Skeleton from "react-loading-skeleton";
import "react-loading-skeleton/dist/skeleton.css";

import "./index.css";

const FallbackPage = () => {
  return (
    <div className="FallbackPage_content">
      <div className="AffiliatePageFallback_block">
        <div className="AffiliatePageFallback_box">
          <Skeleton
            height="100%"
            baseColor="rgba(0, 0, 0, 0.11)"
            highlightColor="rgba(0, 0, 0, 0.2)"
          />
        </div>
        <div className="AffiliatePageFallback_box">
          <Skeleton
            height="100%"
            baseColor="rgba(0, 0, 0, 0.11)"
            highlightColor="rgba(0, 0, 0, 0.2)"
          />
        </div>
      </div>
    </div>
  );
};

export default FallbackPage;
