import React from "react";

import { TitleDecorator } from "../TitleDecorator";
import { BannersList } from "./BannersList";
import css from "./style.module.scss";
import { IBannerProps } from "../MenuListSection";

export const HouseGames = ({
  contentData,
  withoutBigBanners,
  title,
}: {
  contentData: IBannerProps[];
  withoutBigBanners: boolean;
  title: string;
}) => {
  return (
    <>
      <TitleDecorator position="right">{title.toLowerCase()}</TitleDecorator>
      <div className={css.container}>
        <BannersList
          contentData={contentData}
          withoutBigBanners={withoutBigBanners}
        />
      </div>
    </>
  );
};
