import React, { Suspense, useEffect } from "react";
import { useHistory } from "react-router-dom";
import { Helmet } from "react-helmet";
import clsx from "clsx";

import { useGetLocale, useWhiteTheme } from "providers";
import { useDetectRobots, useFetch, useLocalStorage } from "hooks";
import { Typography } from "components";

import LazyComponentLoader from "config/LazyComponentLoader";
import {
  eventAnimationsList,
  LeaderBoardRow,
  GameList,
  Challenges,
  GamesProviders,
  StatSection,
  BetList,
  LeaderboardUser,
  MenuListSection,
} from "./components";
import { IFetchHomePageInfo } from "./types/homeInfo";
import css from "./style.module.scss";

const Banners = React.lazy(() =>
  LazyComponentLoader(import("./components/Banners")),
);

export interface FetchDailyLeaderboard {
  success: boolean;
  leaderboard: LeaderboardUser[];
  message: string;
}

const referralReg = /\?user_id=([0-9]{1,})/;
const sourceReg = /source=(\w{1,})/;

const HomePage = () => {
  const history = useHistory();
  const [token] = useLocalStorage("token");
  const [, setRefer] = useLocalStorage("refer");
  const [, setSourceRefer] = useLocalStorage("sourceRefer");
  const { activeEvent } = useWhiteTheme();
  const isRobot = useDetectRobots();

  const [fetchDailyLeaderboard, dailyLeaderboardResult] =
    useFetch<FetchDailyLeaderboard>("statistics/daily-leaderboard");
  useEffect(() => {
    fetchDailyLeaderboard();
  }, [fetchDailyLeaderboard]);
  const [homePageInfoQuery, homePageInfo] =
    useFetch<IFetchHomePageInfo>("homeinfo/get-all");

  const intl = useGetLocale();

  useEffect(() => {
    homePageInfoQuery();
  }, [homePageInfoQuery]);

  useEffect(() => {
    if (!token) {
      const search = history.location.search;

      const referralResult = search.match(referralReg);
      const sourceResult = search.match(sourceReg);

      if (referralResult) {
        const [url, referralId] = referralResult;

        setRefer(referralId);
      }

      if (sourceResult) {
        const [url, sourceReferralId] = sourceResult;
        setSourceRefer(sourceReferralId);
      }
    }
  }, []);

  const EventAnimation = activeEvent
    ? eventAnimationsList[activeEvent]
    : () => null;

  return (
    <>
      {!history.location.pathname.split("/").includes("page") && (
        <Helmet>
          {/* <!-- Primary Meta Tags --> */}
          <title>Pasino - Crypto Gaming Casino</title>
          <meta name="title" content="Pasino - Crypto Gaming Casino" />
          <meta
            name="description"
            content="Unparalleled crypto gaming
from the makers of FaucetPay! Provably Fair Gambling. Instant
deposits and withdrawals"
          />
          {/* <!-- Open Graph / Facebook --> */}
          <meta property="og:type" content="website" />
          <meta property="og:url" content="https://pasino.com/" />
          <meta
            property="og:title"
            content="Pasino - Crypto Gaming
Casino"
          />
          <meta
            property="og:description"
            content="Unparalleled crypto
gaming from the makers of FaucetPay! Provably Fair Gambling.
Instant deposits and withdrawals"
          />
          <meta property="og:image" content="" />
          {/* <!-- Twitter --> */}
          <meta property="twitter:card" content="summary_large_image" />
          <meta property="twitter:url" content="https://pasino.com/" />
          <meta
            property="twitter:title"
            content="Pasino - Crypto Gaming
Casino"
          />
          <meta
            property="twitter:description"
            content="Unparalleled
crypto gaming from the makers of FaucetPay! Provably Fair
Gambling. Instant deposits and withdrawals"
          />
          <meta property="twitter:image" content="" />
        </Helmet>
      )}

      <div className={clsx(css.root, activeEvent && css[activeEvent])}>
        <Suspense fallback={<div className={css.bannersFallBack} />}>
          {!isRobot && <Banners />}
        </Suspense>
        <MenuListSection homePageInfo={homePageInfo.data} />
        <StatSection
          impressiveWins={homePageInfo?.data?.impressive_wins}
          topWinnings={homePageInfo?.data?.profitable_games}
          recentlyAdded={homePageInfo?.data?.new_games}
          trending={homePageInfo?.data?.trending_games}
        />

        <section>
          <GameList
            title="Best Slots"
            allLink="/slot-games"
            cardSize={248}
            mobile={[
              {
                cardsAmount: 2,
                rowsAmount: 2,
                triggerSize: 600,
              },
            ]}
            data={
              homePageInfo?.data?.best_slots.map(
                ({ id, image_url, name, description }) => {
                  return {
                    img: image_url,
                    url: `slot/${id}`,
                    name,
                    about: description,
                  };
                },
              ) || []
            }
          />
        </section>
        <section>
          <GamesProviders />
        </section>
        <section>
          <GameList
            title="Live Games"
            allLink="/slot-games?category=live"
            cardSize={248}
            mobile={[
              {
                cardsAmount: 2,
                rowsAmount: 2,
                triggerSize: 600,
              },
            ]}
            data={
              homePageInfo?.data?.live_games.map(
                ({ id, image_url, name, description }) => {
                  return {
                    img: image_url,
                    url: `slot/${id}`,
                    name,
                    about: description,
                  };
                },
              ) || []
            }
          />
        </section>
        <section>
          <Challenges />
        </section>

        <section>
          <GameList
            title="Featured Slots"
            allLink="/slot-games"
            cardSize={164}
            rows={2}
            mobile={[
              {
                cardsAmount: 3,
                rowsAmount: 1,
                triggerSize: 600,
              },
              {
                cardsAmount: 2,
                rowsAmount: 1,
                triggerSize: 400,
              },
            ]}
            data={
              homePageInfo?.data?.featured_slots.map(
                ({ id, image_url, name, description }) => ({
                  img: image_url,
                  url: `slot/${id}`,
                  name,
                  about: description,
                }),
              ) || []
            }
          />
        </section>
        <section className={css.item}>
          <Typography className={css.title} variant="h2">
            {intl
              .formatMessage({ id: "homeScreen.winnersRow" })
              .toLocaleLowerCase()}
          </Typography>
          <LeaderBoardRow data={dailyLeaderboardResult?.data?.leaderboard} />
        </section>
        <section className={clsx(css.item, css.betList)}>
          <Typography className={css.title} variant="h2">
            {intl
              .formatMessage({ id: "homeScreen.betsRow" })
              .toLocaleLowerCase()}
          </Typography>

          <BetList />
        </section>
        <EventAnimation />
      </div>
    </>
  );
};

export default HomePage;
