import React from "react";
import clsx from "clsx";

import { Typography } from "components";

import css from "./style.module.scss";

interface IProps {
  img: string;
  descr: string;
  onClickHandler: () => void;
}

const MenuItem = ({ img, descr, onClickHandler }: IProps) => {
  return (
    <div className={clsx(css.item)} onClick={onClickHandler}>
      <img src={img} alt={descr} className={css.icon} />
      <Typography className={css.text}>{descr}</Typography>
    </div>
  );
};

export default MenuItem;
