import React from "react";
import { useGetLocale } from "providers";
import { ReactComponent as CheckCircleOutlineIcon } from "assets/icons/check-circle-outline.svg";

import { Typography } from "components";

import { Template } from "pages/Auth/components";
import css from "./style.module.scss";

export interface Props {
  message?: string;
}

const iconStyle = {
  fontSize: 120,
};

const SuccessMessage = ({ message }: Props) => {
  const intl = useGetLocale();

  return (
    <Template
      title={
        <div className={css.iconWrapper}>
          <CheckCircleOutlineIcon style={iconStyle} />
        </div>
      }>
      <div className={css.successMessageWrapper}>
        <Typography className={css.text}>
          {message ||
            intl.formatMessage({
              id: "form.passwordRecovery.success",
            })}
        </Typography>
      </div>
    </Template>
  );
};

export default SuccessMessage;
