import React, { useRef, useMemo, FC } from "react";
import { Link } from "react-router-dom";
import clsx from "clsx";

import { OptimizedSlider, SliderValues, useAutoScroll } from "components";
import { useWindowSize } from "providers";
import { useDetectRobots } from "hooks";
import { between } from "utils";

import Empty_url from "./empty.png";
import { Header } from "./components";
import { BannerAdornment } from "pages/Home/components/HouseGames/BannersList/components/BannerAdornment";
import { GameListProps } from "./types";
import css from "./style.module.scss";

export const GameList: FC<GameListProps> = ({
  title,
  data = [],
  cardSize = 248,
  rows = 1,
  mobile,
  allLink,
  classes,
}) => {
  const sliderRef = useRef<SliderValues | null>(null);
  const isRobot = useDetectRobots();

  const { width } = useWindowSize();
  const actualMobile = useMemo(
    () =>
      mobile
        ?.sort(
          ({ triggerSize: firstSize }, { triggerSize: secondSize }) =>
            firstSize - secondSize,
        )
        .find(({ triggerSize }) => width < triggerSize),
    [mobile, width],
  );

  const rowsAmount = !!actualMobile ? actualMobile.rowsAmount : rows;
  const cardsAmount = !!actualMobile ? actualMobile.cardsAmount : null;

  const increment = () => {
    sliderRef.current?.increment();
  };
  const decrement = () => {
    sliderRef.current?.decrement();
  };
  const { start, stop } = useAutoScroll(increment, between(3500, 5000));

  return (
    <div className={css.container}>
      <Header
        {...{
          title,
          allLink,
          increment,
          decrement,
        }}
      />
      <div
        className={clsx(
          css.sliderWrapper,
          classes?.body,
          allLink === null && css.withoutAllLink,
        )}>
        <OptimizedSlider
          ref={sliderRef}
          cardWidth={cardSize}
          rows={rowsAmount}
          dataLength={data.length}
          cardsAmount={cardsAmount}
          onMouseOver={stop}
          onMouseLeave={start}>
          <>
            {data.length > 0 &&
              data.map(({ url, img, about, name }) => (
                <Link to={url} className={css.link} key={name}>
                  <img
                    src={!isRobot ? img : Empty_url}
                    alt={name}
                    className={css.img}
                  />
                  <BannerAdornment label={name} info={about} to={url} />
                </Link>
              ))}
          </>
        </OptimizedSlider>
      </div>
    </div>
  );
};
