import React, { FunctionComponent } from "react";
import clsx from "clsx";

import { Button, ButtonProps, CircularProgress, Transition } from "components";

import css from "./style.module.scss";

export interface SubmitButtonProps extends ButtonProps {
  loading?: boolean;
}

const SubmitButton: FunctionComponent<SubmitButtonProps> = ({
  children,
  type = "submit",
  disabled,
  className,
  loading,
  onClick,
  ...rest
}) => {
  const handleClick: ButtonProps["onClick"] = (event) => {
    if (onClick) {
      event.preventDefault();
      onClick(event);
    }
  };

  return (
    <Button
      {...rest}
      onClick={handleClick}
      disabled={disabled || loading}
      className={clsx(css.button, className)}
      type={type}>
      <Transition in={loading} className={css.loadingWrapper} animation="fade">
        <CircularProgress />
      </Transition>
      <Transition in={!loading} animation="fade">
        {children}
      </Transition>
    </Button>
  );
};

export default SubmitButton;
