import { FormatMessageFN } from "providers";
import { Dispatch, SetStateAction } from "react";

export const emailRegEx =
  /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export const usernameRegEx = /^[0-9A-Za-z_]+$/;

export const minimumUserNameLength = 3;
export const maximumUserNameLength = 16;

export const minimumPasswordLength = 6;
export const maximumPasswordLength = 64;

export interface RegisterFields {
  userName: string;
  email: string;
  password: string;
}

export type ErrorSetter = Dispatch<
  SetStateAction<{
    userName: string;
    email: string;
    password: string;
  }>
>;

export const validateUsername = (
  userName: string,
  formatMessage: FormatMessageFN,
) => {
  if (userName !== userName.trim()) {
    return formatMessage({ id: "form.register.errors.userName.trim" });
  }
  if (userName.length < minimumUserNameLength) {
    return formatMessage({
      id: "form.register.errors.userName.short",
    });
  }
  if (userName.length > maximumUserNameLength) {
    return formatMessage({ id: "form.register.errors.userName.long" });
  }
  if (!usernameRegEx.test(userName)) {
    return formatMessage({
      id: "form.register.errors.userName.symbols",
    });
  }

  return "";
};

export const validateEmail = (
  email: string,
  formatMessage: FormatMessageFN,
) => {
  if (!emailRegEx.test(email)) {
    return formatMessage({ id: "form.register.errors.email.format" });
  }

  return "";
};

export const validatePassword = (
  password: string,
  formatMessage: FormatMessageFN,
) => {
  if (password !== password.trim()) {
    return formatMessage({ id: "form.register.errors.password.trim" });
  }
  if (password.length < minimumPasswordLength) {
    return formatMessage({
      id: "form.register.errors.password.short",
    });
  }
  if (password.length > maximumPasswordLength) {
    return formatMessage({ id: "form.register.errors.password.long" });
  }

  return "";
};

export const validatePasswordRepeat = (
  password: string,
  passwordError: string,
  passwordRepeat: string,
  formatMessage: FormatMessageFN,
) => {
  if (!passwordError && password !== passwordRepeat) {
    return formatMessage({
      id: "form.register.errors.passwordRepeat.match",
    });
  }

  return "";
};

export const validate = (
  { userName, email, password }: RegisterFields,
  setter: ErrorSetter,
  formatMessage: FormatMessageFN,
) => {
  const userNameError = validateUsername(userName, formatMessage);
  const emailError = validateEmail(email, formatMessage);
  const passwordError = validatePassword(password, formatMessage);

  setter({
    userName: userNameError,
    email: emailError,
    password: passwordError,
  });

  return !(userNameError || emailError || passwordError);
};

export default validate;
