import React, { FC } from "react";
import { GetCoinImg } from "components";
import css from "./style.module.scss";

interface Props {
  id: string;
  title: string;
  image_url: string;
  description: string;
  reward_coin: string;
  reward_amount: string;
  end_time: string;
  max_win_users_count: string | number;
}

export const getFormattedDate = (end_time: string) => {
  const time = new Date(Number(end_time) * 1000);
  const day = time.getDate();
  const month = time.getMonth() + 1;
  const year = time.getFullYear().toString().slice(2, 4);
  return `${day}.${month > 9 ? month : "0" + month}.${year}`;
};

const Notification: FC<Props> = ({
  id,
  title,
  image_url,
  description,
  reward_coin,
  reward_amount,
  end_time,
  max_win_users_count,
}) => {
  return (
    <div className={css.root}>
      <div className={css.descriptionContainer}>
        <div className={css.imageContainer}>
          <img src={image_url} />
        </div>
        <div className={css.wrapper}>
          <span className={css.title}>{title}</span>
          <span className={css.description}>{description} </span>
        </div>
      </div>
      <div className={css.rewardContainer}>
        <div className={css.rewardTitles}>
          <span>Maximum</span>
          <span>Prize</span>
          <span>Runtime</span>
        </div>
        <div className={css.rewardValuesWrapper}>
          <div>
            {max_win_users_count}
            {` Winner${Number(max_win_users_count) !== 1 ? "s" : ""}`}
          </div>

          <div className={css.reward}>
            {reward_amount}
            <img
              src={`https://cdn.pasino.com/coins/${reward_coin.toLocaleLowerCase()}.png`}
              className={css.coinImg}
            />
          </div>
          <div>until {getFormattedDate(end_time)}</div>
        </div>
      </div>
    </div>
  );
};

export default Notification;
