import React, {
  DetailedHTMLProps,
  FC,
  HTMLAttributes,
  useEffect,
  useRef,
  useState,
} from "react";
import clsx from "clsx";
import { Link } from "react-router-dom";

import { createLinkParamsFromString } from "utils";
import { Typography } from "components";
import { useEventListener } from "hooks";

import { ScrollFunctions } from "pages/Home/components/GameList/types";
import css from "../../style.module.scss";

interface Props
  extends DetailedHTMLProps<HTMLAttributes<HTMLSpanElement>, HTMLSpanElement> {
  label?: string;
  info?: string | JSX.Element;
  to?: string;
  scrollValues?: ScrollFunctions;
}

export const BannerAdornment: FC<Props> = ({
  label,
  info,
  to,
  scrollValues,
  className,
  ...rest
}) => {
  const infoRef = useRef<HTMLDivElement>(null);
  const [isOpenPopper, setIsOpenPopper] = useState(false);

  useEventListener("click", (event) => {
    if (event.target !== infoRef.current && isOpenPopper) {
      setIsOpenPopper(false);
    }
  });

  useEventListener("scroll", () => setIsOpenPopper(false));

  useEffect(() => {
    if (!scrollValues) {
      return;
    }

    const { start, stop, values } = scrollValues;

    if (!isOpenPopper) {
      !values.isStarted && start();
    } else {
      stop();
    }
  }, [isOpenPopper]);

  return (
    <span className={clsx(css.continueContent, className)} {...rest}>
      <Link to={createLinkParamsFromString(to)} className={css.link}>
        <Typography variant="body1" className={css.label}>
          {label}
        </Typography>
      </Link>
    </span>
  );
};
