import React, { FC, Suspense } from "react";
import { LazyHider } from "components";
import LazyComponentLoader from "config/LazyComponentLoader";

import { StatProps } from "./types";

import crashUrl from "assets/homepage/all_games/crash.svg";
import hashdiceUrl from "assets/homepage/all_games/hash_dice.webp";
import plinkoUrl from "assets/homepage/all_games/plinko.webp";
import limboUrl from "assets/homepage/all_games/limbo.webp";
import videoPokerUrl from "assets/homepage/all_games/video_poker.webp";
import diamondsUrl from "assets/homepage/all_games/diamonds.webp";
import kenoUrl from "assets/homepage/all_games/keno.webp";
import rouletteUrl from "assets/homepage/all_games/roulette.webp";
import black_jackUrl from "assets/homepage/all_games/black_jack.webp";
import minesUrl from "assets/homepage/all_games/mines.webp";
import fortuneUrl from "assets/homepage/all_games/fortune_quest.webp";
import towersUrl from "assets/homepage/all_games/towers.webp";

export const originGames: Record<
  string,
  {
    link: string;
    imgUrl: string;
  }
> = {
  crash: {
    link: "crash",
    imgUrl: crashUrl,
  },
  dice: {
    link: "hash-dice",
    imgUrl: hashdiceUrl,
  },
  plinko: {
    link: "plinko",
    imgUrl: plinkoUrl,
  },
  limbo: {
    link: "limbo",
    imgUrl: limboUrl,
  },
  videopoker: {
    link: "video-poker",
    imgUrl: videoPokerUrl,
  },
  diamonds: {
    link: "diamonds",
    imgUrl: diamondsUrl,
  },
  keno: {
    link: "keno",
    imgUrl: kenoUrl,
  },
  roulette: {
    link: "roulette",
    imgUrl: rouletteUrl,
  },
  blackjack: {
    link: "blackjack",
    imgUrl: black_jackUrl,
  },
  wheel: {
    link: "fortunequest",
    imgUrl: fortuneUrl,
  },
  mines: {
    link: "mines",
    imgUrl: minesUrl,
  },
  towers: {
    link: "towers",
    imgUrl: towersUrl,
  },
};

const Content = React.lazy(() => LazyComponentLoader(import("./Content")));

export const StatSection: FC<StatProps> = (props) => {
  return (
    <LazyHider
      sizes={{
        height: 255,
      }}>
      <Suspense fallback={null}>
        <Content {...props} />
      </Suspense>
    </LazyHider>
  );
};
