import React, { FC } from "react";
import clsx from "clsx";

import { Button, ButtonProps } from "components";

import { BannerAdornment } from "../BannerAdornment";
import { createGameForwardedLink } from "../";
import css from "../../style.module.scss";

interface Props extends ButtonProps {
  to: string;
  label?: string;
  bigBanner?: boolean;
  info?: string;
}

export const GameBanner: FC<Props> = ({
  to,
  label,
  info,
  bigBanner,
  children,
  className,
  ...props
}) => {
  return (
    <div className={clsx(css.flex, bigBanner && css.bigFlex)}>
      <Button
        {...props}
        className={clsx(
          css.rootBanner,
          css.bannerRoot,
          bigBanner && css.bigBanner,
          className,
        )}
        component={createGameForwardedLink(to, label)}>
        {children}
      </Button>
      <BannerAdornment
        className={clsx(bigBanner && css.bigBannerLabel)}
        label={label}
        info={info}
        to={to}
      />
    </div>
  );
};
