import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import { useGetLocale } from "providers";

import {
  FormHelperText,
  Input,
  InputProps,
  Modal,
  Transition,
} from "components";
import { useFetch } from "hooks";
import { SubmitButton, Template } from "pages/Auth/components";
import {
  validatePassword,
  validatePasswordRepeat,
} from "pages/Auth/tabs/Register/validateFields";
import { ModalsProps } from "config/routes";

import Loading from "./Loading";
import SuccessMessage from "./SuccessMessage";
import ErrorMessage from "./ErrorMessage";
import css from "./style.module.scss";

interface FetchData {
  success: boolean;
  message: string;
}

const PasswordRecovery = ({ isOpen, code }: ModalsProps) => {
  const intl = useGetLocale();
  const history = useHistory();

  const [password, setPassword] = useState("");
  const [passwordRepeat, setPasswordRepeat] = useState("");

  const [errors, setErrors] = useState({
    password: "",
    passwordRepeat: "",
  });

  const [fetch, { loading, data, error }] = useFetch<FetchData>(
    "account/reset-password",
  );

  const handleChangePassword: InputProps["onChange"] = (event) => {
    setErrors({
      ...errors,
      password: "",
    });
    setPassword(event.target.value);
  };

  const handleChangePasswordRepeat: InputProps["onChange"] = (event) => {
    setErrors({
      ...errors,
      passwordRepeat: "",
    });
    setPasswordRepeat(event.target.value);
  };

  const handleResetPassword = () => {
    const passwordError = validatePassword(password, intl.formatMessage);
    const passwordRepeatError = validatePasswordRepeat(
      password,
      passwordError,
      passwordRepeat,
      intl.formatMessage,
    );

    if (!passwordError && !passwordRepeatError) {
      fetch({ variables: { code, password } });
    } else {
      setErrors({
        password: passwordError,
        passwordRepeat: passwordRepeatError,
      });
    }
  };

  const handleClose = () => {
    history.push({ pathname: "/" });
  };

  const renderView = () => {
    if ((isOpen && ((data && !data.success) || !code)) || error) {
      return <ErrorMessage message={error || data?.message} />;
    }

    if (isOpen && data && data.success) {
      return <SuccessMessage message={data?.message} />;
    }

    return (
      <Template
        title={intl.formatMessage({
          id: "form.passwordRecovery.title",
        })}
        description={intl.formatMessage({
          id: "form.passwordRecovery.description",
        })}>
        <Input
          id="password-reset-input"
          label={intl.formatMessage({
            id: "form.passwordRecovery.passwordLabel",
          })}
          value={password}
          error={!!errors.password}
          helperText={errors.password}
          onChange={handleChangePassword}
        />

        <Input
          id="repeat-password-reset-input"
          label={intl.formatMessage({
            id: "form.passwordRecovery.passwordRepeatLabel",
          })}
          value={passwordRepeat}
          error={!!errors.passwordRepeat}
          helperText={errors.passwordRepeat}
          onChange={handleChangePasswordRepeat}
        />

        {error && (
          <div className={css.errorWrapper}>
            <FormHelperText error>{error}</FormHelperText>
          </div>
        )}

        <SubmitButton
          className={css.submitButton}
          onClick={handleResetPassword}>
          {intl.formatMessage({
            id: "form.requestNewPassword.button",
          })}
        </SubmitButton>
      </Template>
    );
  };

  return (
    <Modal open={isOpen} onClose={handleClose}>
      <div className={css.innerWrapper}>
        <Transition
          className={css.loadingWrapper}
          animation="fade"
          in={loading}>
          <Loading />
        </Transition>

        <Transition animation="fade" in={!loading}>
          {renderView()}
        </Transition>
      </div>
    </Modal>
  );
};

export default PasswordRecovery;
