import React, { useRef } from "react";
import { Link } from "react-router-dom";
import { Typography, OptimizedSlider, SliderValues } from "components";
import { useWhiteTheme } from "providers";
import { Arrows } from "../GameList/components/Header/Arrows";
import { provadersData } from "./utils/index";
import css from "./style.module.scss";

export const GamesProviders = () => {
  const sliderRef = useRef<SliderValues | null>(null);
  const cardWidth = 170;
  const { isWhiteThemeActive } = useWhiteTheme();
  const increment = () => {
    sliderRef.current?.increment();
  };
  const decrement = () => {
    sliderRef.current?.decrement();
  };

  const providers = isWhiteThemeActive
    ? provadersData.white
    : provadersData.black;

  return (
    <div className={css.container}>
      <div className={css.title_and_buttons_container}>
        <Typography variant="h2" className={css.title}>
          Games Providers
        </Typography>
        <div className={css.buttons_container}>
          <Arrows
            onClicks={{
              leftArrow: decrement,
              rightArrow: increment,
            }}
          />
        </div>
      </div>
      <div>
        <OptimizedSlider
          cardWidth={cardWidth}
          dataLength={providers.length}
          ref={sliderRef}>
          {providers.map(({ img, name }) => (
            <Link
              to={`slot-games?provider=${name}`}
              className={css.slideElement}>
              <img src={img} alt={name} className={css.imgElement} />
            </Link>
          ))}
        </OptimizedSlider>
      </div>
    </div>
  );
};
