import React from "react";
import { ReactComponent as ErrorOutlineIcon } from "assets/icons/error-outline.svg";
import { useGetLocale } from "providers";
import { Typography } from "components";

import { Template } from "pages/Auth/components";
import css from "./style.module.scss";

export interface Props {
  message?: string;
}

const errorStyle = { fontSize: 120 };

const ErrorMessage = ({ message }: Props) => {
  const intl = useGetLocale();

  return (
    <Template
      title={
        <div className={css.iconWrapper}>
          <ErrorOutlineIcon style={errorStyle} />
        </div>
      }>
      <div className={css.errorMessageWrapper}>
        <Typography className={css.text}>
          {message ||
            intl.formatMessage({
              id: "form.confirmEmail.error",
            })}
        </Typography>
      </div>
    </Template>
  );
};

export default ErrorMessage;
