import LazyComponentLoader from "config/LazyComponentLoader";
import React, { Suspense } from "react";

const Snowflakes = React.lazy(() =>
  LazyComponentLoader(import("components/Snowflakes")),
);

export const eventAnimationsList = {
  christmas: () => (
    <Suspense fallback={null}>
      <Snowflakes />
    </Suspense>
  ),
};
