import React, { FunctionComponent } from "react";

import { DialogContent, DialogContentText, DialogTitle } from "components";
import css from "./style.module.scss";

interface Props {
  title?: React.ReactNode;
  description?: React.ReactNode;
  height?: number;
}

const Template: FunctionComponent<Props> = ({
  title,
  description,
  children,
  height,
}) => {
  return (
    <div
      className={css.wrapper}
      style={{
        maxHeight: height,
        height,
      }}>
      <div className={css.innerWrapper}>
        <div className={css.header}>
          {title && (
            <DialogTitle id="auth-dialog-title" className={css.title}>
              {title}
            </DialogTitle>
          )}

          {description && (
            <DialogContentText
              id="auth-dialog-description"
              className={css.description}>
              {description}
            </DialogContentText>
          )}
        </div>
        <div className={css.root}>
          <DialogContent className={css.content}>
            <form className={css.form}>{children}</form>
          </DialogContent>
        </div>
      </div>
    </div>
  );
};

export default Template;
