import React, { useRef } from "react";

import { Typography, OptimizedSlider, SliderValues } from "components";
import { useNotification } from "providers";

import { Arrows } from "../GameList/components/Header/Arrows";
import { Card } from "./components";
import css from "./style.module.scss";

export const Challenges = () => {
  const { notification } = useNotification();
  const sliderRef = useRef<SliderValues | null>(null);
  const increment = () => {
    sliderRef.current?.increment();
  };
  const decrement = () => {
    sliderRef.current?.decrement();
  };

  if (!notification.challenges.length) {
    return null;
  }

  return (
    <div className={css.container}>
      <div className={css.title_and_buttons_container}>
        <Typography variant="h2" className={css.title}>
          Challenges
        </Typography>
        <div className={css.buttons_container}>
          <Arrows
            onClicks={{
              leftArrow: decrement,
              rightArrow: increment,
            }}
          />
        </div>
      </div>
      <OptimizedSlider
        dataLength={notification.challenges.length}
        cardWidth={350}
        isGrow={false}
        ref={sliderRef}>
        {notification.challenges.map((challenge) => (
          <Card {...challenge} />
        ))}
      </OptimizedSlider>
    </div>
  );
};
